    if (plan.price === 0) {
      // ÂÖçË≤ªÁâà - Áõ¥Êé•ÈñãÂßã
      this.router.navigate(['/dashboard']);
      return;
    }
    
    // ‰ªòË≤ªÊñπÊ°à - Ë∑≥ËΩâÂà∞ÊîØ‰ªòÈ†ÅÈù¢
    this.selectedPlan.set(plan.id);
    this.isUpgrading.set(true);
    
    // TODO: ÂØ¶ÁèæÊîØ‰ªòÊµÅÁ®ã
